var projectID = window.location.pathname.substring(9);

/**
 * 
 */
function initGame(): void {
    let canvas = <HTMLCanvasElement>document.getElementById('renderCanvas');
    let fpsLabel = document.getElementById('fpsLabel');
    fpsLabel!.style.display = 'none';
    if (canvas !== null && fpsLabel !== null) {
        game = new Game(canvas, fpsLabel);
        game.createScene(datas).animate();  // 
    }
}

function runBtn(): void {
    if (game) {
        game.createScene(datas).animate();
    }
}

function toggleDebug(): void {
    if (game) {
        game.toggleDebug();
    }
}


function init(): void {
    // 
    if (dataLoaded && dataLoaded2) {
        initGame();
    } else {
        setTimeout(init, 500);
    }
}


function loadResources(): void {

    var projectID = window.location.pathname.substring(9);
    Config.projectID = projectID;
    // project data
    axios.post('/api/getProject', { projectID: Config.projectID })
        .then(response => {
            var data = response.data;
            if (data.code === '0000') {
                // console.log(data.data);
                Config.projectData = data.data;
                Config.projectName = data.data.project.name;
                Config.userID = data.data.owner.id;
                Config.username = data.data.owner.username;
                // assetsscenesceneassets
                editor.call('getAssets');
                // document.title = Config.projectName + ' - ';
                // editor.call('toolbar.project.set', Config.projectName);
            } else {
                console.error('load project data error: ' + data.message);
            }
        })
        .catch(
            error => {
                console.error('load project data error: ');
                console.error(error);
            }
        );


    editor.method('getAssets', () => {
        // assets data
        axios.post('/api/getAssets', { projectID: Config.projectID })
            .then(response => {
                var data = response.data;
                if (data.code === '0000') {
                    // console.log(data.data);
                    Config.assetsData = data.data;
                    BabylonLoader.assetsData = data.data;
                    // editor.call('initAssets', Config.assetsData);
                    // assetsscenesceneassets
                    editor.call('getScenes');
                    // 
                    if(data.data.assets) {
                        for(let key in data.data.assets) {
                            if(data.data.assets[key].type === 'table') {
                                Config.tableAssetsID = key;
                                Config.tableName = data.data.assets[key].name;
                            }
                        }
                    }
                    if(Config.tableAssetsID !== '') {
                        editor.call('loadTable');
                    } else {
                        dataLoaded = true;
                    }
                } else {
                    console.error(data.message);
                }
                // ep.emit('settings', response.data);
            })
            .catch(
                error => {
                    console.error(error);
                }
            );
    });

    editor.method('getScenes', () => {
        // scenes data
        axios.post('/api/getScenes', { projectID: Config.projectID})
            .then(response => {
                var data = response.data;
                if (data.code === '0000') {
                    let lastScene: number = data.data.last;
                    Config.scenesData = data.data.scenes[lastScene];
                    Config.sceneIndex = lastScene;
                    Config.sceneName = Config.scenesData.name;

                    document.title = Config.projectName + ' - ' + Config.sceneName + ' - ';
                    // console.log(Config.scenesData);
                    dataLoaded2 = true;
                    // editor.emit('scene:raw', Config.scenesData);
                    // editor.call('toolbar.scene.set', Config.scenesData.name);
                } else {
                    console.error(data.message);
                }
                // ep.emit('settings', response.data);
            })
            .catch(
                error => {
                    console.error(error);
                }
            );
    });


    editor.method('loadTable', () => {
        // 
        var path = window.location.href;
        var paramsData: any = {};
        paramsData['name'] = Config.tableName;
        paramsData['projectID'] = projectID;
        paramsData['id'] = Config.tableAssetsID;
        axios
            .get('/api/table/acquire', {
                params: paramsData,
            })
            .then(function (response) {
                let data = response.data;
                // console.log(data);
                if (data.code === '0000') {
                    // json
                    let tempData = JSON.stringify(data.data);
                    // 
                    tempData = escape2Html(tempData);
                    // json
                    // hot1.loadData(JSON.parse(tempData).table);
                    // console.log(JSON.parse(tempData).table);
                    datas = JSON.parse(tempData).table;
                    Config.tableData = datas;
                    dataLoaded = true;
                } else {
                    // Do nothing
                    console.log('load not right: ' + data.message);
                }
            })
            .catch(function (error) {
                console.log('load error: ' + error);
            });
    });



}

loadResources();


function loadScene(): void {


    var sceneData: any;
    axios.post('/api/getScenes', { projectID: projectID })
        .then(response => {
            var data = response.data;
            if (data.code === '0000') {
                let lastScene: number = data.data.last;
                sceneData = data.data.scenes[lastScene];
                console.log(sceneData);
                document.title = sceneData.name + ' | ';
                // path1 = sceneData.path1;
                // path2 = sceneData.path2;
                // console.log('path1: ' + sceneData.path1);
                // console.log('path2: ' + sceneData.path2);
                // dataLoaded2 = true;
            } else {
                console.error(data.message);
            }
            // ep.emit('settings', response.data);
        })
        .catch(
            error => {
                console.error(error);
            }

        );
}

