//HTML< -> &lt;
function html2Escape(sHtml: any) {
    var temp: any = document.createElement('div');
    temp.textContent != null
        ? (temp.textContent = sHtml)
        : (temp.innerText = sHtml);
    var output = temp.innerHTML;
    temp = null;
    return output;
}

//HTML&lt; -> <
function escape2Html(str: any) {
    var temp: any = document.createElement('div');
    temp.innerHTML = str;
    var output = temp.innerText || temp.textContent;
    temp = null;
    return output;
}

