import { VeryCamera } from "../lib";

export class Config {

    public static prefix: string = '/assets/';


    public static projectID: string = '';
    public static projectName: string = '';
    public static projectData: any = {};

    public static userID: string = '';
    public static username: string = '';

    public static assetsData: any = {};

    public static scenesData: any = {};
    public static sceneIndex: number = 0;
    public static sceneName: string = '';

    public static tableAssetsID: string = '';
    public static tableName: string = '';
    public static tableData: any = null;


    public static cameras: VeryCamera[] = [];


    public constructor() {

    }


    public static addCamera(camera: VeryCamera): void {
        Config.cameras.push(camera);
    }


}